export default "\n/* RESEARCH */\n\n/* useful link */\n\n/* https://stackoverflow.com/questions/114543/how-to-horizontally-center-a-div */\n\n.research-row {\n  /* width: 100%; */\n  /* margin-bottom: 20px; */\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  justify-content: center;\n  /* align-items: center; */\n  /* box-orient: horizontal; */\n  /* box-flex: 0; */\n  /* box-direction */\n}\n\n.research-container {\n  font-family: Merriweather;\n  position: relative;\n  padding: 10px 10px;\n  /* border-radius: 4px; */\n  /* height: 400px; */\n  min-height: 390px;\n  height: 20%;\n  width: 32%;\n  margin: 10px 5px;\n  box-shadow: 0.5px 0.5px 1px 1px rgba(0, 0, 0, 0.274);\n}\n\n.research-container > img,\n.img-bottom {\n  height: 60%;\n  width: 100%;\n  border: 1px solid #eee;\n}\n\n/* .teaser {\n  width: 100%;\n  height: 0;\n  padding-top: 80%;\n  border: 1px solid #eee;\n  background-size: cover;\n  background-position: top left;\n} */\n\n/* .research-row .research-container+.research-container {\n  margin-left: 8px;\n} */\n\n.research-row:after,.research-row:before {\n  content: \"\";\n  display: table;\n  clear:both;\n}\n\n.research-row .research-container h5 {\n  margin-top: 12px;\n  font-size: 18px;\n}\n\n.research-row .research-container h6 {\n  margin: 5px 0;\n  font-size: 15px;\n}\n\n.research-row .research-container p {\n  margin-bottom: 0px;\n  font-size: 12px;\n}\n\n.research-container:hover {\n  border-radius: 4px;\n  /* border: 0.07em solid rgb(56, 248, 255); */\n  box-shadow: 0.5px 0.5px 1.5px 1.5px rgba(0, 0, 0, 0.5);\n}\n\n/* @media screen and (min-width: 1001px) {\n  .research-container {\n    width:32%\n  }\n} */\n\n@media screen and (max-width: 1001px) {\n  .research-container {\n    width:45%;\n  }\n}\n\n@media screen and (max-width: 771px) {\n  .research-container {\n    width:95%;\n  }\n}\n\n#hk-research {\n  max-width: 1000px;\n  margin: 40px auto;\n  background: #ffffff;\n  /* clear: both; */\n}\n\n/* @media screen and (max-width: 480px) {\n  .research-container {\n    width:80%;\n  }\n} */\n\n.img-change {\n  position: relative;\n  display: inline-block;\n}\n\n.img-change .img-top {\n  display: none;\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 100%;\n  z-index: 99;\n}\n\n.img-change:hover .img-top {\n  display: inline;\n}\n/*# sourceMappingURL=data:application/json;base64, */"