export default "\n/* NAVIGATION */\n#hk-nav {\n  display: block;\n  max-width: 1000px;\n  width: 100%;\n  /* font-family: Georgia, 'Times New Roman', Times, serif; */\n  font-family: Merriweather;\n  font-size: 0;\n  line-height: 0;\n  margin: 0 auto;\n  padding: 0;\n  background-color: #ff9e9e;\n  color: #ffffff;\n  /* border-radius: 0 0 20px 20px; */\n}\n#hk-nav ul {\n  margin: 0;\n  padding: 0;\n  overflow: hidden;\n}\n#hk-nav ul li {\n  float: left;\n  padding: 0 auto;\n  margin: 0 auto;\n}\n#hk-nav ul li a {\n  display: block;\n  font-size: 15px;\n  line-height: normal;\n  text-align: center;\n  color: #ffffff;\n  padding: 15px 20px;\n}\n#hk-nav ul li a:hover {\n  color: #ff3d3d;\n  -webkit-transition: all .3s ease-in-out;\n  -moz-transision: all .3s ease-in-out;\n  transition: all .3s ease-in-out;\n}\n@media screen and (max-width: 480px) {\n  #hk-nav ul li a {\n    margin: 0 10px;\n    font-size: 14px;\n    padding: 15px 5px;\n  }\n}\n@media screen and (max-width: 340px) {\n  #hk-nav ul li a {\n    margin: 0 5px;\n    font-size: 14px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"